#root {
    width: 80%;
    margin: auto;
}

.header {
    display: flex;
    flex-direction: row;
    background: #ff742b;
    padding: 10px;
}

.logo-wrapper {
    margin-right: 20px;
    flex: 1 1 0;
}

.logo-wrapper img {
    width: 48px;
    margin-right: 5px;
}

.logo-name {
    color: #fff;
    display: inline-block;
    font-size: 1em;
}

.input-search-box {
    flex: 2 1 0;
    margin-right: 20px;
}

.input-search-box input {
    width: 90%;
    padding: 10px;
}

.powered-by {
    display: inline-block;
    flex: 1 1 0;
}

.powered-by img {
    width: 60px;
    height: 15px;
}

.menu {
    width: 100px;
    border: 1px solid #eee;
    position: absolute;
}

.custom-form {
    position: absolute;
    background: #fff;
    margin-left: 100px;
    margin-top: 150px;
}

.custom-form input {
    padding: 10px;
    margin: 10px;
}

.search-panel {
    display: flex;
    padding: 10px;
}

.search-panel-result {
    margin-left: auto;
}

.search-panel span, .mainButton {
    margin-right: 10px;
}

.mainButton {
    border: 1px solid #eee;
    background: #fff;
    padding: 5px;
}

.dropDownBtn {
    background: #fff;
    border: 0;
    width: 100%;
    padding: 10px;
    border-bottom: 1px solid #eee;
}

.post-list-item {
    background: #f6f6ef;
    padding: 10px;
    border-bottom: 1px solid #eee;
    font-size: 13px;
}

.post-title-panel {
    margin-bottom: 5px;
}

.post-list-item span {
    border-right: 3px solid #eee;
    margin-right: 5px;
    padding-right: 5px;
}

.post-list-item span:last-child {
    border: none;
}

.post-data-line {
    margin-bottom: 10px;
}

.pagination {
    width: 40%;
    margin: auto;
    margin-top: 25px;
    display: flex;
    list-style: none
}

.pagination li{
    cursor: pointer;
    color: black;
    margin: 0 3px;
    padding: 5px 10px;
    text-decoration: none;
    transition: background-color .3s;
}

.page-link:active
.page-link:link,
.page-link:visited,
.page-link:hover,
.page-link:focus{
    outline: none;
}

li.active {
    border: 2px solid orange;
}

